import CustomModal from 'app/components/CustomModal';
import 'assets/css/order.scss';
import PropTypes from 'prop-types';
import React, { useEffect, useState } from 'react';
import { Modal } from 'react-bootstrap';
import { FormattedMessage } from 'react-intl';
import { Button } from 'reactstrap';

DialogNewEditTracking.propTypes = {
    trackingDetail: PropTypes.object,

    show: PropTypes.bool,
    isNew: PropTypes.bool,

    onHide: PropTypes.func,
    onDelete: PropTypes.func,
    onUpdate: PropTypes.func,
    onNew: PropTypes.func
};

function DialogNewEditTracking({
    trackingDetail,
    show,
    onHide,
    onDelete,
    onUpdate,
    onNew,
    isNew = false
}) {
    const [values, setValues] = useState({
        code: '',
        checked: false
    });

    useEffect(() => {
        if (show) {
            setValues({
                code: trackingDetail?.code || '',
                checked: trackingDetail?.checked || false
            });
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [show]);

    const handleInputChange = e => {
        const reg = /^[0-9]*$/;
        const check = reg.test(e.target.value);
        if (!check) return;

        setValues({
            ...values,
            [e.target.name]: e.target.value
        });
    };

    const handleBoxChange = () => {
        setValues({
            ...values,
            checked: !values.checked
        });
    };

    const handleUpdate = () => {
        const params = {
            id: trackingDetail?.id || '',
            code: values.code || trackingDetail?.code || '',
            checked: values.checked ? 1 : 0
        };
        if (onUpdate) {
            onUpdate(params);
            resetValues();
        }
    };

    const handleDelete = () => {
        onDelete(trackingDetail?.id);
    };

    const handleNew = () => {
        const params = {
            code: values.code,
            checked: values.checked ? 1 : 0
        };
        if (onNew) {
            onNew(params);
            resetValues();
        }
    };

    const resetValues = () => {
        setValues({
            code: '',
            checked: false
        });
    };

    return (
        <CustomModal
            show={show}
            onHide={onHide}
            title={isNew ? 'Thm mi tracking' : 'Cp nht m tracking'}
        >
            <Modal.Body className="overlay overlay-block cursor-default">
                <div className="form-group row">
                    <div className="col-12">
                        <label>M tracking</label>
                        <input
                            name="code"
                            value={values.code}
                            className="form-control"
                            onChange={handleInputChange}
                        />
                    </div>
                </div>

                <div className="form-group row">
                    <div className="col-12">
                        <div className="form-check">
                            <input
                                className="form-check-input"
                                type="checkbox"
                                id="checkTracking"
                                onChange={handleBoxChange}
                                checked={values.checked}
                            />
                            <label
                                className="form-check-label"
                                htmlFor="checkTracking"
                            >
                                <FormattedMessage id="TRACKING.RECEIVED" />
                            </label>
                        </div>
                    </div>
                </div>
            </Modal.Body>

            <Modal.Footer>
                <button
                    type="button"
                    onClick={onHide}
                    className="btn btn-light btn-elevate"
                >
                    <FormattedMessage id="GLOBAL.BUTTON.CANCEL" />
                </button>
                {isNew ? (
                    <Button color="primary" onClick={handleNew}>
                        <FormattedMessage id="GLOBAL.BUTTON.ADD" />
                    </Button>
                ) : (
                    <>
                        <Button onClick={handleDelete} color="danger">
                            <FormattedMessage id="GLOBAL.BUTTON.DELETE" />
                        </Button>
                        <Button color="primary" onClick={handleUpdate}>
                            <FormattedMessage id="GLOBAL.BUTTON.UPDATE" />
                        </Button>{' '}
                    </>
                )}
            </Modal.Footer>
        </CustomModal>
    );
}

export default DialogNewEditTracking;
